.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #8f06a4 21.43%,
    /* #8693db 50.63%,
    #9415d9 100%, */ #481381eb 117.04%
      /* #406aff 21.43%,
    #3bace2 50.63%,
    #b936ee 100%,
    #ff56f6 117.04% */
